import React, { useState, useEffect, Fragment } from 'react';
import SidebarStyle from './styles/SidebarStyle';
import SidebarItem from './SidebarItem';
import BackgroundBlur from './styles/BackgroundBlur';

export default function Sidebar() {
  const [sidebarWidth, setSidebarWidth] = useState(75);

  const [windowWidth, setWindowWidth] = useState(0);
  useEffect(() => {
    setWindowWidth(window.innerWidth);
    window.addEventListener('resize', () => {
      setWindowWidth(window.innerWidth);
    });

    return window.removeEventListener('resize', () => {
      setWindowWidth(window.innerWidth);
    });
  }, []);

  return (
    <Fragment>
      <SidebarStyle
        onMouseOver={() => {
          setSidebarWidth(250);
          setWindowWidth(window.innerWidth);
        }}
        onMouseLeave={() => {
          setSidebarWidth(75)
          setWindowWidth(window.innerWidth);
        }}
        width={sidebarWidth}
      >
        <SidebarItem to="/" textAlign={sidebarWidth === 75 ? 'center' : 'left'}>
          <i className="fas fa-home" />
          <p className={sidebarWidth === 75 ? 'hide' : 'show'}>Home</p>
        </SidebarItem>

        <SidebarItem to="/skills" textAlign={sidebarWidth === 75 ? 'center' : 'left'}>
          <i className="fab fa-react"></i>
          <p className={sidebarWidth === 75 ? 'hide' : 'show'}>Skills</p>
        </SidebarItem>
      </SidebarStyle>
      <BackgroundBlur width={windowWidth - sidebarWidth} display={sidebarWidth === 75 ? 'hidden' : 'block'} />
    </Fragment>
  )
}
